
.nu-breadcrumbs {
		list-style: none;
		margin:0;
		padding:0;
		display: inline-block;
}
.nu-breadcrumbs li {
		display: inline-block;
		margin: 0 4px 0 0;
}
.nu-breadcrumbs li a {
		display: block;
    	line-height: 16px;
    	font-size: 16px;
    	text-decoration: none;
}
.nu-breadcrumbs li svg {
    	width: 16px;
    	height: 10px;
}



