
.nu-modal_open {
	overflow: hidden;
}
.nu-modal__overlay {
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	position: fixed;
	overflow: auto;
	z-index: 9001;
	font-size: 14px;
	-webkit-font-smoothing: antialiased;
	background: rgba(255, 255, 255, 0.9);
	opacity: 0;
	transition: opacity 0.3s;
	-webkit-transform: translate3D(0, 0, 0);
	        transform: translate3D(0, 0, 0);
	-webkit-perspective: 500px;
}
.nu-modal__overlay.is-visible {
	opacity: 1;
}
.nu-modal {
	box-sizing: border-box;
	background: #fff;
	box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 16px rgba(0,0,0,0.12);
	position: absolute;
	top: 20%;
	left: 50%;
	width: 80%;
	margin-bottom: 50px;
	max-width: 800px;
	border-radius: 2px;
	z-index: 1;
	opacity: 0;
}
.nu-modal.is-visible {
	-webkit-transform: translateX(-50%);
	        transform: translateX(-50%);
	opacity: 1;
}
.nu-modal__close {
	position: absolute;
	cursor: pointer;
	color: #222C38;
	width: 24px;
	height: 24px;
	transition: opacity .3s;
	right: 15px;
	top: 15px;
	z-index: 1;
	border-radius: 50%;
}
.nu-modal__close:hover {
	opacity: .4;
}
.nu-modal__close svg {
	width: 24px;
	height: 24px;
}
.nu-modal__title {
	border-bottom: 1px solid #eaeaea;
	padding: 15px 64px 15px 30px;
	text-align: left;
}
.nu-modal__title h2 {
	font-weight: 500;
	font-size: 22px;
	margin: 0;
}
.nu-modal__content {
	display: block;
	padding: 15px 30px;
	line-height: 1.5;
	text-align: left;
}
@media screen and (max-width: 600px) {
.nu-modal__content {
		padding: 24px 15px;
}
.nu-modal {
		width: 100%;
		min-height: 100%;
		left: 0;
		top: 0;
		-webkit-transform: scale(0.9);
		        transform: scale(0.9);
		margin: 0;
}
.nu-modal.is-visible {
		-webkit-transform: none;
		        transform: none;
}
}



